import React, { Component } from "react";
import PerfectScrollbar from "react-perfect-scrollbar";
import SimpleMessage from "../components/SimpleMessage";
import JobLocationMessage from "../components/JobLocationMessage";
import UploadResumeMessage from "../components/UploadResumeMessage";
import AlertMessage from "../components/AlertMessage";

class ChatBoxContainer extends Component {
  state = {};

  render() {
    return (
      <div className="chat-box__container pt-8">
        <PerfectScrollbar className="h-100 relative px-24">
          <SimpleMessage
            message="Hello, Welcome to Elepha.io"
            time="09:00 AM"
            currentUserID="222"
            senderUserID="22"
          />
          <div className="py-16" />
          <JobLocationMessage
            message="Hello, Welcome to Elepha.io"
            time="09:00 AM"
            currentUserID="222"
            senderUserID="22"
            region={["mumbai", "pune", "asam", "panjab", "calcutta"]}
          />
          <div className="py-16" />
          <UploadResumeMessage
            message="Hello, Welcome to Elepha.io"
            time="09:00 AM"
            currentUserID="222"
            senderUserID="22"
            region={["mumbai", "pune", "asam", "panjab", "calcutta"]}
          />
          <div className="py-16" />
          <SimpleMessage
            message="Hello, Welcome to Elepha.io"
            time="09:00 AM"
            currentUserID="22"
            senderUserID="22"
          />
          <div className="py-16" />
          <SimpleMessage
            message="Hello, Welcome to Elepha.io"
            time="09:00 AM"
            currentUserID="232"
            senderUserID="22"
          />
          <div className="py-16" />
          <AlertMessage city="mumbai" phone="01975208815" />
        </PerfectScrollbar>
      </div>
    );
  }
}

export default ChatBoxContainer;
